import React, { useState } from "react";
import { Pane, Heading, Button, Link } from 'evergreen-ui';

import { InlineIcon } from '@iconify/react';

const ProjectLink = ({ url, icon, alt }) => (
    <Button
        is="a"
        href={url}
        marginLeft={10}
        marginRight={10}
        height={32}
    >
        <InlineIcon height={20} icon={icon} alt={alt} style={{textShadow: "3px  4px 2px #EDEDED"}}/>
    </Button>
)


const ProjectBadge = ({ img, link, alt, hovered }) => {
    const imageComponent = <img src={img} alt={alt} style={{opacity: hovered ? 1 : 0.2, margin: 2 }} />;
    return link ? <Link margin={4} href={link}>{imageComponent}</Link> : imageComponent
}


const Project = ({ project }) => {
    const [hovered, setHovered] = useState(false);

    return (<Pane clearfix>
        <Pane
            elevation={1}
            float="left"
            style={project.style && project.style}
            height="15em"
            width="15em"
            margin={24}
            display="flex"
            justifyContent="center"
            alignItems="center"
            flexDirection="column"
            onMouseEnter={() => setHovered(true)}
            onMouseLeave={() => setHovered(false)}
        >

            <Pane
                marginTop={20}
                height="33%"
            >
                {project.badges.map(badge => <ProjectBadge key={badge.label} hovered={hovered} {...badge} />)}
            </Pane>
            <Pane
                height="33%"
            >
                <Heading margin={16} size={600} style={{textShadow: "1px  2px 2px #EDEDED"}}>{project.title}</Heading>
            </Pane>
            <Pane
                alignContent="bottom"
                marginBottom={20}
                height="33%"
            >
                {project.links.map(link => <ProjectLink key={link.alt} {...link} />)}
            </Pane>
        </Pane>
    </Pane>)
}

export default Project
