import React from "react";
import { Pane } from 'evergreen-ui';

// Icons
import boxIcon from '@iconify/icons-feather/box';

import PageHeader from '../common/PageHeader';

import projectsData from './ProjectsStore'
import Project from './Project';


const Projects = () => (
  <Pane
    flexWrap="wrap"
    wordWrap="break-word"
    padding={16}
  >

    <Pane>
      <PageHeader icon={boxIcon} text="Projects" />
    </Pane>

    <Pane
      display="flex"
      flexWrap="wrap"
      justifyContent="center"
      alignItems="center"
      padding={16}
    >
      {projectsData.map(project => <Project key={project.title} project={project} />)}
    </Pane>
  </Pane>
)

export default Projects;
